import { Injectable } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { filter, map } from 'rxjs/operators';
import { InventoryBinaryService, InventoryService } from '@c8y/client';
import { AlertService, gettext, ModalService, Status } from '@c8y/ngx-components';
import { saveAs } from 'file-saver';
import { isUndefined } from 'lodash-es';
import { TranslateService } from '@ngx-translate/core';

@Injectable()
export class BinaryFileDownloadService {

  constructor(private router: Router,
              private route: ActivatedRoute,
              private inventoryService: InventoryService,
              private alertService: AlertService,
              private modalService: ModalService,
              private translate: TranslateService,
              private inventoryBinary: InventoryBinaryService) {
  }

  run() {
    this.router.events
      .pipe(
        filter((event) => { return event instanceof NavigationEnd
          && this.route.snapshot.queryParams.download; }),
        map(() => this.route.snapshot.queryParams.download))
      .subscribe(async (downloadId) => {
        try {
          const { data } = await this.inventoryService.detail(downloadId);
          if ( !isUndefined(data.c8y_IsBinary) ) {
            this.showDownloadModal(data);
          } else {
            const alertMessage = this.translate.instant(
              gettext('Could not download the file: object with ID "{{ id }}" is not a valid binary.'),
              { id: data.id }
            );
            this.alertService.danger(alertMessage);
          }
        } catch (ex) {
          this.alertService.addServerFailure(ex);
        }
      });
  }

  async showDownloadModal(data) {
    try {
      const modalBody = this.translate.instant(
        gettext('You are about to download file "{{ fileName }}". Do you want to proceed?'),
        { fileName: data.name }
      );
      await this.modalService.confirm(
        gettext('File download'),
        modalBody,
        Status.INFO,
        { ok: gettext('Download') }
      );
      const binary = await this.getBinary(data.id);
      const fileBinary = new File([binary], data.name, { type: data.contentType });
      saveAs(fileBinary);
    } catch (e) {
      // empty body :(
    }
  }

  private async getBinary(binaryId) {
    let binary;
    try {
      const res = await this.inventoryBinary.download(binaryId);
      binary = await res.text();
    } catch (ex) {
      const msg = gettext('Could not get the binary.');
      this.alertService.danger(msg);
    }

    return binary;
  }

}
